<template>
  <div>
    <h2 class="font-weight-bold">Bezwaren & Klachten  Gemeente Amsterdam</h2>

    <p>
      Heeft u bezwaren of klachten over een object in dit register?
    </p>

    <p>
      Er is een standaard procedure bij uw Gemeente beschikbaar om bezwaren en klachten in te dienen, grondslag artikel 6:5 van de Algemene wet bestuursrecht.
      De kern van deze bezwaarprocedure is dat u bezwaar indient tegen een bestuursbesluit dat genomen is door een van de bestuursorganen van de gemeente.
      Bestuursorganen zijn: College, Burgemeester, Gemeenteraad. In Amsterdam worden de Bestuurscommissies ook aangemerkt als bestuursorgaan.
    </p>

    <p>
      Bezwaar indienen doet u door middel van een brief, meer informatie daarover treft u op <a href="link">LINK</a>
    </p>
  </div>
</template>

